* {
  box-sizing: border-box;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ol, ul, li, article, footer, header, menu, nav, section, summary, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

/*
Meant for font-face declaration
*/
@font-face {
  font-family: "Roboto";
  src: url("/assets/fonts/Roboto/Roboto-Regular/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Medium";
  src: url("/assets/fonts/Roboto/Roboto-Medium/Roboto-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Heavy";
  src: url("/fonts/Roboto/Roboto-Bold/Roboto-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.cv-container {
  max-width: 960px;
  margin: 20px auto;
  box-shadow: 0 0 10px rgba(88, 88, 95, 0.12);
  background-color: #fff;
  border: 1px solid #e8ecf0;
}
.cv-container .cv-title {
  text-align: center;
  margin: 25px 0;
  text-transform: uppercase;
  font-family: "Roboto Medium", sans-serif;
  color: #1a409a;
}

.header {
  margin: 40px 0;
  display: flex;
  font-family: "Roboto", sans-serif;
  color: #333333;
}
.header__abbr {
  width: 25%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  margin-top: 3px;
}
.header__circle {
  border: 1px solid #1a409a;
  padding: 10px;
  font-size: 40px;
  border-radius: 50%;
  margin-right: 20px;
}
.header__name {
  width: 75%;
}
.header__name-title {
  color: #1a409a;
  font-size: 45px;
  margin-bottom: 10px;
  margin-left: 20px;
}
.header__name-sub-title {
  font-size: 20px;
  margin-bottom: 20px;
  color: #1a409a;
  margin-left: 20px;
}
.header__details {
  display: flex;
  margin-left: 20px;
}
.header__details p {
  display: flex;
}
.header__details p span {
  display: flex;
  justify-content: center;
  width: 20px;
  margin-right: 10px;
}
.header__details p i {
  color: #1a409a;
}
.header__details p:first-child {
  padding-bottom: 5px;
}
.header__details-left {
  margin-right: 40px;
}

.cv-info {
  display: flex;
  font-family: "Roboto", sans-serif;
  color: #333333;
}
.cv-info__left-panel {
  width: 25%;
  display: flex;
  justify-content: flex-end;
  border-right: 1px dashed rgba(51, 51, 51, 0.5);
  position: relative;
  padding-bottom: 30px;
}
.cv-info__left-panel-title {
  font-family: "Roboto Medium", sans-serif;
  margin-right: 20px;
  color: #1a409a;
  text-align: right;
}
.cv-info__left-panel-circle {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 0px;
  right: -8px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 1px solid rgba(51, 51, 51, 0.5);
}
.cv-info__details {
  width: 75%;
  padding-right: 75px;
  padding-bottom: 30px;
}
.cv-info__details-info {
  margin-left: 20px;
}
.cv-info__details-info ul li {
  display: flex;
  margin-bottom: 2px;
}
.cv-info__details-info ul li span {
  padding-right: 10px;
}
.cv-info__details-info ul li span i {
  color: rgba(51, 51, 51, 0.8);
}

.timeline {
  display: flex;
  font-family: "Roboto", sans-serif;
  color: #333333;
  padding: 0 25px;
}
.timeline__info {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  border-right: 1px dashed rgba(51, 51, 51, 0.5);
}
.timeline__details {
  border: 1px solid #1a409a;
  border-radius: 4px;
  padding: 20px;
  margin-top: 20px;
  margin-right: 50px;
  position: relative;
  margin-bottom: 40px;
}
.timeline__details h2 {
  font-family: "Roboto Medium", sans-serif;
  color: #1a409a;
  padding-bottom: 5px;
}
.timeline__details h3 {
  font-family: "Roboto", sans-serif;
  color: #111111;
  padding-bottom: 5px;
}
.timeline__details ul {
  margin-left: 5px;
}
.timeline__details ul li {
  display: flex;
}
.timeline__details ul li span {
  width: 12px;
  font-size: 14px;
}
.timeline__details ul li span i {
  color: #1a409a;
}
.timeline__details-arrow {
  width: 0;
  height: 0;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 20px solid #1a409a;
  position: absolute;
  right: -20px;
  top: 20px;
}
.timeline__details-bullet {
  position: absolute;
  background-color: #fff;
  top: 27px;
  right: -64px;
  border: 2px solid #1a409a;
  width: 25px;
  height: 25px;
  display: block;
  border-radius: 50%;
}
.timeline__details-bullet-circle {
  position: absolute;
  background-color: #1a409a;
  top: 33px;
  right: -58px;
  border: 2px solid #1a409a;
  width: 13px;
  height: 13px;
  display: block;
  border-radius: 50%;
}
.timeline__title {
  width: 50%;
}
.timeline__title-text {
  font-family: "Roboto Medium", sans-serif;
  color: #111111;
  margin-left: 50px;
  width: 125px;
  height: 125px;
  border: 7px solid #1a409a;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.timeline__title-arrow {
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-left: unset;
  border-right: 20px solid #1a409a;
  position: absolute;
  left: -25px;
  top: 40px;
}

