* {
  box-sizing: border-box;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ol, ul, li, article, footer, header, menu, nav, section, summary, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}


